/* You can add global styles to this file, and also import other style files */
.container{
    font-family: Helvetica, Arial;
    width: 80%;
    position: relative;
    height: 100%;
    margin: 0px auto;
    margin-top: 20px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-shadow: 0px 0px 5px #ddd;
    background: #EEE;
    overflow:hidden;
}

footer{
    width: 80%;
    margin: 0px auto;
    margin-top: 20px; 
    color: gray;
}
header{
    padding: 15px;
    padding-top: 30px;
    background: #C30E4A;
    color: white;
}

header h1{
    margin: 0px;
    margin-left: 15px;
    margin-bottom: 5px;
    
}

.title-subtitle{
   
    border-bottom: 1px solid white;
    border-top: 1px solid white;
    padding-top: 5px;
    
}

.logo{
    width: 86px;
    height: 86px;
    float: right;
    position: relative;
    bottom: 13px;
    
}

header p{
    color: white;
    width: 100%;
    font-size: 12px;
}

.clearfix{
    float: none;
    clear: both;
    text-decoration: none;
    list-style: none;
}

.bordeNegro{
    border: 4px solid black;
}

.bordeVerde{
    border: 4px solid green;
}

.fondoA{
    background: orange;
}


.btn{
    font-size: 13px;
    border: 1px solid #ccc;
    border-color: #1976D2;
    border-radius: 4px;
    box-shadow: 0px 0px 5px #ddd;
    padding: 5px;
    text-decoration: none;
    color: white;
    background: #0074d9;
    cursor: pointer;
    margin-left: 5px;
}

.btn-add-fav{
    float: right;
    padding: 5px;
    margin-left: 20%;
    margin-right: 20px;
    margin-bottom: 20px;
    background: white;
    border-radius: 4px;
    font-size: 14px;
    font-weight: bold;
    border: 1px solid #ddd;
    color: #aa24a5;
    text-decoration: none;
    transition: all 500ms;
    
}

.btn-add-fav:hover{
    color: white;
    background: #aa24a5;
    border: 1px solid #7D1979;
}

.fav-box{
    width: 90%;
    border: 1px solid #ddd;
    box-shadow: 0px 0px 5px 2px #ddd;
    padding: 30px;
    padding-left: auto;
    padding-right: auto;
    color: gray;
    background: white;
    margin: 20px;
   color: black;
   
}

.custom-input{
    border-radius: 3px;
    padding: 5px;
    border: 1px solid #ccc;
    width: 80%;
    margin-bottom: 25px;
    font-size: 14px;
    
}

textarea{
    height: 80px;
}

.form-cont{
    margin-left: 15%;
    margin-right: 15%;
   
}

label{
    font-size: 16px;
    color: #4C4C4C;
}

.btn-submit{
    color: white;
    background: #C30E4A;
    border: 1px solid #C30F3F;
    border-radius: 3px;
    float: right;
    font-weight: bold;
    padding: 5px;
    cursor: pointer;
    margin-right: 18%;
}


